@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@500&display=swap);
body {
  margin: 0 auto;
  font-family: 'Heebo', sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer{
    color: #fafafa;
    background-color: black;
    text-align: center;
    margin-top: -2rem;
    padding-top: 0.4rem;
    padding-bottom: 2rem;
}
footer svg{
    margin: 0 1rem;
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
}
footer svg:hover{
     cursor: pointer;
     transform:scale(1.3) ;
     -webkit-transform:scale(1.3) ;
     -moz-transform:scale(1.3) ;
     -ms-transform:scale(1.3) ;
     -o-transform:scale(1.3) ;
}
header{
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    color: #fafafa;
    background-color: black;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;

}

header p{
    font-size: 3rem;
}
header .hamburguer-menu{
    width: 3.5rem;
    margin-right: 3rem;
    
}
header .hamburguer-menu img{
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
}

div .menu-mobile{
    position: fixed;
    right: 2000px;
}

div .menu-mobile-open{
    width:100%;
    text-align: center;
    position: fixed;
    z-index: 1;
    width: 100%;
    color: #fafafa;
    background-color: rgba(0,0,0,0.8);
    height: 90vh;
    

}

ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-right: 5rem;

}
ul li{
    margin-top: 3rem;
    font-weight: bold;
    font-size: 1.7rem;
}
ul li a{
    color: #fafafa;
    text-decoration: none;
}

.section{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    color: #fafafa;
    background-image: url(/basement/static/media/background.bee2126a.jpg);
    margin: 0 auto;
}
.section h1{
    margin-top: 5rem;
    font-size: 4rem;
}


.neon-word{
        color: #fff;
        margin-top: 30%;
        text-shadow:
            0 0 5px rgb(221, 219, 219),
            0 0 10px rgb(36, 37, 39),
            0 0 20px rgb(239, 241, 241),
            0 0 40px rgb(75, 75, 75),
            0 0 80px rgb(65, 65, 65),
            0 0 90px rgb(55, 55, 55),
            0 0 100px rgb(45, 45, 45),
            0 0 140px rgb(35, 35, 35),
            0 0 180px rgb(25, 25, 25);
    }

img{
    width: 70%;
}
@media (min-width: 600px){
    .section{
        height: 120vh;
    }
    img{
        width: 60%;
    }
}

section{
    background-image: url(/basement/static/media/background.bee2126a.jpg);
    color: #fafafa;
    padding-top: 2rem;
    padding-bottom: 2rem;
    text-align: center;
    
}
img{
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  transition: all 300ms ease-in-out;
  -webkit-transition: all 300ms ease-in-out;
  -moz-transition: all 300ms ease-in-out;
  -ms-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
}
img:hover{
  filter: grayscale(0%);
  -webkit-filter: grayscale(0%);
}

.carousel-section{
  display: flex;
  flex-direction: column;
  align-items: center;

}
img{
  margin-bottom: 1.5rem;
}

section p{
    padding: 2rem;
}
.thumbnails img{
  height: 30vh;
  margin: 1rem;
}
@media (min-width: 600px){
 
    .carousel-section{
      margin-top: 0;
      padding-top: 15rem;
      text-align: center;
      justify-content: center;
      display: flex;
      flex-direction: column;
    }
    .image-gallery{
      width: 90%;
      margin: 0 auto;
      z-index: 0;
      height: 120vh;
    } 

    .image-gallery-thumbnail{
      border: 4px solid transparent;
}
   .image-gallery-svg{
     display: none;
   }
   section p{
     width: 60%;
     margin: 0 auto;
   }
   .thumbnails{
    display: flex;
    justify-content: center;
  }
  .thumbnails img{
    width: 20%;
    height: 30vh;
    margin: 1rem;
  }
  }
section.reservation{
    margin-top: -1.5rem;
    background-color: rgb(5, 5, 5);
    background-image: none;
    padding-top: 2.5rem;
}
.reservation-form{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.reservation-form div{
   background-color: #fadbdb;
   width: 60%;
   padding: 0.2rem;
   color: red;
   border: 2px solid red;
   
}

input{
    width: 80%;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    height: 2rem;
    margin-bottom: 0.5rem;
}
button{
    margin-top: 2rem;
    width: 80%;
    height: 3rem;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: inherit;
}
@media (min-width: 600px){
    input{
        width: 40%;
    }
    button{
        width: 40%;
    }
    .reservation-form div{
        width: 40%;
       
        
     }
}
